.banner-container {
    background-color: #f1f1f1;
    height: 663px;
    width: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    
  }
  
  .banner-content {
    max-width: auto;
    width: 100%;
    display: flex;
    flex-direction: row; /* Sắp xếp theo hàng ngang */
    align-items: center;
    justify-content: space-between; /* Căn chỉnh giữa hai phần */
    
  }
  
  .banner-text {
    flex: 1; /* Chia đều kích thước */
    width: 577;
    height: 173;
    top: 237px;
    left: 100px;
  }
  
  .banner-image {
    background: url('D:\Sturdy\web_and_aplication\xdpmWeb\web-clothing\src\asset\images/Banner web-clothing.png') no-repeat center center / cover;
    flex: 1;
    display: flex;
    justify-content: center; /* Căn giữa theo chiều ngang */
    align-items: flex-end; /* Đẩy hình ảnh xuống dưới */
    height:100%; /* Chiếm toàn bộ chiều cao */
    padding-bottom: 20px; /* Khoảng cách từ dưới lên */
    object-fit: contain;
  }
  
  .banner-image img {
    max-width: 100%;
    max-height: 800px; /* Giới hạn chiều cao tối đa */
    height: auto;
    width: auto;
    object-fit: contain; /* Đảm bảo hình ảnh giữ tỷ lệ */
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  }
  
  
  .banner-text h1 {
    font-family: Integral CF;
    font-weight: 700;
    font-size: 64px;
    line-height: 64px;
    letter-spacing: 0%;
    vertical-align: middle;
    
  }
  
  .banner-text > p {
    font-family: Satoshi;
    font-weight: 400;
    font-size: 16px;
  
    line-height: 22px;
    letter-spacing: 0%;
    
    color: #666;
  }
  
  .shop-button {
    font-family: Satoshi;
    font-weight: 500;
    font-size: 16px;
    line-height: 100%;
    letter-spacing: 0%;
  
    background-color: black;
    color: white;
    width: 210;
    height: 52;
    top: 507px;
    left: 100px;
    gap: 12px;
    border-radius: 62px;
    padding-top: 16px;
    padding-right: 54px;
    padding-bottom: 16px;
    padding-left: 54px;
    
  }
  .left-align {
    text-align: left;
    align-items: flex-start;
  }
  
  
  /* .banner-image img {
    max-width: 600px;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  } */
  
  .banner-stats{
    display: flex;
    width: 596;
    height: 74;
    top: 607px;
    left: 100px;
    gap: 32px;
    
    align-items: center;
    
  }
  
  .stat-number {
    margin: auto;
    margin-top: 40px;
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 700;
    font-size: 40px;
    line-height: 60%;
    letter-spacing: 0%;
    vertical-align: middle;
    
  }
  
  .stat-text {
  font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  letter-spacing: 0%;
  margin-bottom: 60px;
  
  }
  
  .divider {
    width: 2px;
    height: 70px;
    background-color: #ccc;
  }
  
  
  .banner-brands {
    width: 1390px;
    height: 122px;
    background-color: black;
    color: white;
    padding: 2rem;
   
    display: flex;
    justify-content:space-evenly;
    gap: 1rem;
    font-size: 1rem;
    font-weight: bold;
  }
  .brand-versace {
    font-family: "Times New Roman", serif;
    font-weight: bold;
    font-size: 2.5rem;
  }
  
  .brand-zara {
    font-family: "Arial", sans-serif;
    font-style: italic;
    font-size: 2.5rem;
  }
  
  .brand-gucci {
    font-family: "Courier New", monospace;
    text-transform: uppercase;
    font-size: 2.5rem;
  }
  
  .brand-prada {
    font-family: "Georgia", serif;
    letter-spacing: 2px;
    font-size: 2.5rem;
  }
  
  .brand-calvin {
    font-family: "Verdana", sans-serif;
    font-size: 2.5rem;
  }
  